import classNames from 'classnames';
import { CATEGORY } from 'common';
import useCategory from 'hooks/useCategory';
import useProfile from 'hooks/useProfile';
import Cookies from 'js-cookie';
import React, { useEffect, useRef, useState } from 'react';
import { NavLink, useHistory } from 'react-router-dom';
import styles from './style.module.scss';

function HeaderMobi() {
  const [showNav, setShowNav] = useState(false);
  const { setCategory } = useCategory();
  const toggleContainer = useRef<any>();
  const { profile } = useProfile();
  const history = useHistory();
  useEffect(() => {
    window.addEventListener('click', onClickOutsideHandler);
    return () => window.removeEventListener('click', onClickOutsideHandler);
  }, []);

  const handleClick = (value?: number) => {
    if (!value) {
      setShowNav(false);
      return;
    }

    const newCategory = {
      category: value,
      page: 1,
    };
    setShowNav(false);
    setCategory(newCategory);
  };

  const onClickOutsideHandler = (event: any) => {
    if (!toggleContainer.current.contains(event.target)) {
      setShowNav(false);
    }
  };

  const handleLogout = () => {
    Cookies.remove('token');
    Cookies.remove('refreshToken');
    window.location.reload();
  };

  return (
    <div ref={toggleContainer}>
      <div className={classNames({ [styles.showNav]: showNav }, styles.mobiNavigation)}>
        <nav className={styles.mobiMainNav}>
          <ul>
            <li>
              <NavLink to="/" onClick={() => handleClick()}>
                Home
              </NavLink>
            </li>
            <li>
              <NavLink to="/article" onClick={() => handleClick(CATEGORY.HTML_CSS)}>
                HTML-CSS
              </NavLink>
            </li>
            <li>
              <NavLink to="/article" onClick={() => handleClick(CATEGORY.JAVASCRIPT)}>
                JavaScript
              </NavLink>
            </li>
            <li>
              <NavLink to="/article" onClick={() => handleClick(CATEGORY.REACTJS)}>
                ReactJs
              </NavLink>
            </li>
            <li>
              <NavLink to="/about" onClick={() => handleClick()}>
                About
              </NavLink>
            </li>
            <li>{profile && <NavLink to={`/profile/${profile.id}`}>profile</NavLink>}</li>
            <li>
              {profile ? (
                <NavLink to="/" onClick={() => handleLogout()}>
                  Logout
                </NavLink>
              ) : (
                <NavLink to="/login" onClick={() => handleClick()}>
                  Login
                </NavLink>
              )}
            </li>
          </ul>
        </nav>
        <div className={styles.circleContainer}>
          <div className={styles.circle}>
            <button className={classNames(styles.close, styles.btn)} onClick={() => setShowNav(!showNav)}>
              <i className="fas fa-times"></i>
            </button>
            <button className={classNames(styles.open, styles.btn)} onClick={() => setShowNav(!showNav)}>
              <i className="fas fa-bars"></i>
            </button>
          </div>
        </div>
      </div>
    </div>
  );
}

export default HeaderMobi;
