import { HomeOutlined, SettingOutlined, TableOutlined, UserOutlined, ReadOutlined } from '@ant-design/icons';
import { Menu } from 'antd';
import classNames from 'classnames';
import useToggleSideNav from 'hooks/useToggleSideNav';
import React, { useEffect, useState } from 'react';
import { Link, useLocation } from 'react-router-dom';
import styles from './styles.module.scss';

const { SubMenu } = Menu;

export default function SideNav() {
  const { collapsed } = useToggleSideNav();
  const location = useLocation();
  const [selectedKey, setSelectedKey] = useState('1');

  const routes = [
    {
      key: '1',
      text: 'Manager',
      url: '/manager',
      icon: <HomeOutlined />,
    },
    {
      key: '2',
      text: 'Post',
      url: '/manager/post',
      icon: <TableOutlined />,
    },
    {
      key: '3',
      text: 'Users',
      url: '/manager/users',
      icon: <UserOutlined />,
    },
    {
      key: '4',
      text: 'About',
      url: '/manager/about-setting',
      icon: <SettingOutlined />,
    },
    {
      key: '5',
      text: 'Category',
      url: '/manager/category',
      icon: <ReadOutlined />,
    },
    // {
    //   key: '5',
    //   text: 'Settings',
    //   icon: <SettingOutlined />,
    //   children: [
    //     {
    //       key: '3.1',
    //       text: 'Setting 1',
    //       url: '/settings/setting1',
    //     },
    //     {
    //       key: '3.2',
    //       text: 'Setting 2',
    //       url: '/settings/setting2',
    //     },
    //   ],
    // },
  ];

  useEffect(() => {
    routes.forEach((route) => {
      if (location.pathname.startsWith(route.url || '###')) {
        setSelectedKey(route.key);
      }
      // if (route.children) {
      //   route.children.forEach((childRoute) => {
      //     if (location.pathname.startsWith(childRoute.url || '###')) {
      //       setSelectedKey(childRoute.key);
      //     }
      //   });
      // }
    });
  }, [location.pathname]);

  return (
    <div
      className={classNames({
        [styles.sideNav]: true,
        [styles.sideNavCollapsed]: collapsed,
      })}
      style={{ width: collapsed ? 80 : 250, transition: 'width 0.3s' }}
    >
      <Link className={styles.logo} to="/">
        HOME
      </Link>
      <Menu selectedKeys={[selectedKey]} defaultOpenKeys={[]} mode="inline" inlineCollapsed={collapsed}>
        {routes.map((route) => {
          return (
            <Menu.Item key={route.key} icon={route.icon}>
              <Link to={route.url}>{route.text}</Link>
            </Menu.Item>
          );
        })}
      </Menu>
    </div>
  );
}
