import { Dropdown, Menu } from 'antd';
import logo from 'assets/logo/logo.png';
import React from 'react';
import { NavLink, useHistory } from 'react-router-dom';
import ListMenu from './ListMenu';
import styles from './style.module.scss';
import noAvatar from 'assets/images/no-avatar.png';
import useProfile from 'hooks/useProfile';
import Cookies from 'js-cookie';
function Header() {
  const history = useHistory();
  const logout = () => {
    Cookies.remove('token');
    Cookies.remove('refreshToken');
    window.location.reload();
  };
  const { profile } = useProfile();
  const menu = (
    <Menu style={{ minWidth: 220 }}>
      <Menu.Item key="1" onClick={() => history.push(`/profile/${profile.id}`)}>
        profile
      </Menu.Item>
      <Menu.Item key="2" onClick={logout}>
        logout
      </Menu.Item>
      {profile?.role === 'admin' && (
        <Menu.Item key="3" onClick={() => history.push('/manager')}>
          manager
        </Menu.Item>
      )}
    </Menu>
  );

  return (
    <div className={styles.header}>
      <div className={styles.logo}>
        <NavLink to="/">
          <img src={logo} alt="logo" />
        </NavLink>
      </div>
      <ListMenu />
      {profile ? (
        <div className={styles.headerControl}>
          <div className={styles.menuWrapperControl}>
            <div className={styles.menuControlItem}>
              <Dropdown overlay={menu} trigger={['click']}>
                <div className={styles.avatar}>
                  <img src={profile.avatar || noAvatar} alt="" />
                </div>
              </Dropdown>
            </div>
          </div>
        </div>
      ) : (
        <NavLink to="/login">Login</NavLink>
      )}
    </div>
  );
}

export default Header;
