import PageHeader from 'components/PageHeader';
import SideNav from 'components/SideNav';
import useProfile from 'hooks/useProfile';
import React, { lazy, Suspense } from 'react';
import { Redirect, Route, Switch } from 'react-router-dom';
import styles from './style.module.scss';
function ManagerWrapper() {
  const Manager = lazy(() => import('pages/Manager'));
  const Post = lazy(() => import('pages/Post'));
  const User = lazy(() => import('pages/User'));
  const AboutSetting = lazy(() => import('pages/AboutSetting'));
  const Category = lazy(() => import('pages/Category'));

  const { profile } = useProfile();
  if (profile?.role !== 'admin') {
    return <Redirect to="/" />;
  }
  return (
    <div className={styles.pageWrapper}>
      <SideNav />
      <div className={styles.mainWrapper}>
        <PageHeader />
        <div className={styles.pageContent}>
          <Suspense fallback={null}>
            <Switch>
              <Route exact path="/manager" component={Manager} />
              <Route exact path="/manager/post" component={Post} />
              <Route exact path="/manager/users" component={User} />
              <Route exact path="/manager/about-setting" component={AboutSetting} />
              <Route exact path="/manager/category" component={Category} />
            </Switch>
          </Suspense>
        </div>
      </div>
    </div>
  );
}

export default ManagerWrapper;
